.About {
  height: auto;
  margin: auto;
}

.AboutUpperContainer {
  /* width: 100%; */
  height: auto;
  padding-top: 35px;
  padding-left: 25px;
  padding-right: 25px;
  padding-bottom: 25px;
  margin-bottom: 70px;
  background-color: #343434;
}

.leftContainer {
  float: left;
  width: 57%;
}

.Name {
  color: #d4d4d4;
  padding-bottom: 15px;
  font-weight: bold;
  font-size: 36px;
}

.Job {
  color: #d4d4d4;
  font-weight: lighter;
  font-size: 22px;
}

.NameUndArbeit {
  border-bottom: 2px solid #888888;
  width: fit-content;
  padding-bottom: 20px;
}

.rightContainer {
  float: right;
  width: 37%;
  margin-right: 40px;
}

.ProfielBild {
  -webkit-filter: opacity(1);
  filter: opacity(1);
}

.clear {
  clear: both;
}

.AboutText {
  width: 100%;
  height: auto;
}

.Text {
  text-align: center;
  color: #d4d4d4;
}

.followMe {
  margin-top: 30px;
}

.followMe h2 {
  color: #d4d4d4;
}

@media only screen and (max-width: 747px) {
  .rightContainer {
    float: none;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
  }

  .leftContainer {
    float: none;
    width: 100%;
  }

  .NameUndArbeit {
    text-align: center;
    width: auto;
  }

  .tabel td {
    vertical-align: top;
    display: flex;
    margin-right: 35px;
  }
}
