.Navbar {
  position: absolute;
  z-index: 2;
  width: auto;
  top: 0px;
  margin-bottom: 100px;
  right: 10%;
}

.Navbar ul {
  list-style: none;
}

.Navbar li {
  display: inline-block;
  margin-left: 35px;
  margin-top: 20px;

  position: relative;
}

.Navbar a {
  color: #d4d4d4;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
}

.Navbar a::before {
  content: "";
  display: block;
  height: 4px;
  background-color: #00b719;

  position: absolute;
  bottom: -7px;
  width: 0%;

  transition: all ease-in-out 250ms;
}

.active {
  padding-bottom: 3px;
  border-bottom: 4px solid #00b719;
  transition: all ease-in-out 250ms;
  position: relative;
}

.Navbar a:hover::before {
  width: 100%;
}

.NavbarMobiel {
  display: none;
}

